@media screen and (min-width: 320px) and (max-width: 575px) {
	.header {
		&_container {
			max-width: 300px;
		}
		
		&__top.show{
			min-height: 100vh;
			min-width: 100vw;
			background-color: #0aa566;
			position: absolute;
			left: 0;
			top: 0;
			max-width: 100vw;
			padding: 0 20px;
			display: flex;
			flex-direction: column-reverse;
			justify-content: flex-end;
		}

		&__inner{
			flex-direction: column;
			margin-top: 130px;
		}

		&__nav {
			margin-top: 70px;
		}

		&__top {
			display: none;
		}

		&__personal::after {
			display: none;
		}
	}

	.header__logo  {
		left: 38%;
	}

	.burger {
		&__btn {
			display:block;
			background-color: transparent;
			border: none;
			cursor: pointer;
			position: absolute;
			right: 20px;
			top: 25px;
		}

		&__img {
			height: 40px;
		}
	}

	.nav {
		&__list {
			flex-direction: column;
		}

	}

	.nav__item + .nav__item {
		margin-left: 0px;
		margin-top: 40px;
	}

	.social {
		margin-left: 0px;
		margin-top: 40px;
	}

	
	.show {
		display: block;
	}

	.hidden {
		display: none;
	}

	.smoothies {
		padding: 40px 0 40px;

		&__container {
			max-width: 300px;
		}

		&__title {
			font-size: 27px;
		}

		&__text {
			margin-top: 20px;
			line-height: 26px;
		}

		&__features {
			margin-top: 10px;
		}
	}

	.features {

		&__list {
			max-width: 200px;
			margin: 0 auto;
		}

		&__item {
			border-width: 0px 0px 2px 0px;
		}

		&__item.not {
			border-width: 0px 0px 2px 0px;
		}

		&__item:last-child {
			border-width: 0px 0px 0px 0px;
		}
	}

	.about {
		padding: 40px 0;
		min-height: 0px;

		&__content {
			max-width: 270px;
		}

		&__img {
			display: none;
		}

		&__wrapper {
			padding-left: 0px;
			margin: 0 auto;
		}

		&__title {
			font-size: 28px;
		}

		&__title::after {
			top: 65px;
			left: 40px;
			width: 186px;
		}

		&__list {
			margin-top: 25px;
			font-size: 13px;
		}
		
		&__btn {
			width: 225px;
			margin-top: 20px;
			font-size: 13px;
			height: 45px;
		
		}

		&__btn::after {
			background-size: 70%;
			top: 17px;
			right: 8px;
		}
	}

	.products {
		&__title {
			margin-top: 15px;
			font-size: 14px;
		}

		&__title--small {
			margin-top: 5px;
			font-size: 13px;
		}

		&__img {
			height: 165px;
		}

		&__list {
			flex-wrap: nowrap;
			overflow-x: scroll;
		}

		&__item {
			min-width: 250px;
			padding: 25px 0 25px;
		}

		&__btn {
			margin-top: 25px;
			width: 140px;
			height: 45px;
			font-size: 14px;
		}
	}

	.story {
		padding: 0 0 60px;

		&__container {
			max-width: 250px;
		}

		&__wrapper {
			flex-direction: column;
			align-items: center;
		}

		&__intro {
			padding-top: 30px;
		}
		
		&__title {
			font-size: 35px;
		}

		&__text {
			margin-top: 10px;
			font-size: 15px;
			line-height: 35px;
		}

		&__img-wrapper::before {
			display: none;
		}

		&__btn {
			width: 190px;
			margin-top: 30px;
			font-size: 16px;
			height: 45px;
		}

		&__btn::after {
			background-size: 70%;
			top: 19px;
			right: 12px;
		}

		&__img {
			margin-top: 45px;
			height: 250px;
		}
	}

	.testimonials{
		padding-top: 20px;
		min-height: 320px;

		
		&__slider .slick-arrow.slick-next {
			bottom: -70px;
			right: 33%;

		}

		&__slider .slick-arrow.slick-prev {
			bottom: -70px;
			left: 33%;
		}

		&__title {
			font-size: 25px;
		}

		&__feedback {
			line-height: 28px;
			font-size: 16px;
		}
	
		&::after {
			display: none;
		}

		&__img {
			display: none;
		}
	}

	.slider__item {
		padding-top: 35px;
		background-size: 500px;
		background-position: left 50px bottom 130px;
	}


	.slider__item {
		padding-top: 10px;
		background-image: none;
	}

	.blog {
		padding: 30px 0 30px;
	}

	.blog__container {
		max-width: 250px;
	}

	.blog__wrapper {
		flex-direction: column;
		align-items: center;
	}
	.blog__title {
		font-size: 40px;
	}

	.blog__img {
		height: 180px;
		margin-top: 30px;
	}

	.article__wrapper {
		padding-top: 50px;
		padding-left: 0px;
		margin: 0 auto;
	}

	.article {
		max-width: 240px;
	}

	.article__title {
		font-size: 25px;
	}

	.article__text {
		line-height: 28px;
		margin-top: 25px;
	}

	.blog__title-wrapper::after {
		display: none;
	}

	.instagram__title {
		padding-bottom: 15px;
	}

	.instagram__item::after {
		display: none;
	}

	.footer__container {
		max-width: 230px;
	}

	.footer__social-wrapper {
		padding-left: 0px;
	}
	
	.footer__input {
		width: 240px;
		height: 50px;
	}

	.footer__social-wrapper::before {
		display: none;
	}

	.footer__top-wrapper {
		padding-top: 20px;
    	flex-direction: column;
		align-items: center;
	}

	.information {
		margin: 25px 0 0;
	}

	.information__list {
		margin-top: 10px;
	}

	.information__link {
		line-height: 26px;
	}

	.payment {
		margin: 25px 0 0 0;
	}

	.payment__list {
		margin-top: 20px;
	}

	.footer__subscribe {
		max-width: 255px;
	}

	.footer__btn {
		display: none;
	}

	.footer__top {
		padding-bottom: 20px;
	}

	.up__btn {
		display: none;
	}
}

